import React, { useState, useEffect } from "react";
import * as XLSX from "xlsx";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

const API_URL = "https://script.google.com/macros/s/AKfycbxAk1Pn7at9H1w6fmGqaMSCFFUy4DAy9wUfp4BPHLE4d8tExCW2vS12Q38TMxpPGzxv/exec"; // ����� ���� URL � Google Apps Script

function App() {
  const [user, setUser] = useState(null);
  const [records, setRecords] = useState([]);
  const [brand, setBrand] = useState("��");
  const [date, setDate] = useState("");
  const [clientName, setClientName] = useState("");
  const [filter, setFilter] = useState({ brand: "", manager: "", from: "", to: "" });

  // �����������
  const login = async (username, password) => {
    const res = await fetch(`${API_URL}?action=login&username=${username}&password=${password}`);
    const data = await res.json();
    if (data.success) setUser(data.user);
    else alert("������� ���� ��� ������");
  };

  // ��������� ������
  const fetchRecords = async () => {
    const res = await fetch(`${API_URL}?action=records&user=${user.role === "admin" ? "all" : user.username}`);
    const data = await res.json();
    setRecords(data);
  };

  useEffect(() => {
    if (user) fetchRecords();
  }, [user]);

  // ��������� ������
  const addRecord = async () => {
    if (!date || !clientName) return alert("������ ����");
    await fetch(`${API_URL}?action=addRecord`, {
      method: "POST",
      body: JSON.stringify({ manager: user.username, brand, date, clientName }),
    });
    fetchRecords();
  };

  // ��������� �������
  const markPurchase = async (id) => {
    await fetch(`${API_URL}?action=updateRecord&id=${id}&purchase=1`);
    fetchRecords();
  };

  // ������� � Excel
  const exportToExcel = () => {
    const ws = XLSX.utils.json_to_sheet(records);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "CRM");
    XLSX.writeFile(wb, "crm_export.xlsx");
  };

  if (!user) {
    return <LoginForm onLogin={login} />;
  }

  const filtered = records.filter((r) =>
    (!filter.brand || r.brand === filter.brand) &&
    (!filter.manager || r.manager === filter.manager) &&
    (!filter.from || r.date >= filter.from) &&
    (!filter.to || r.date <= filter.to)
  );

  // ���� ��� �������
  const brandsData = Object.values(
    filtered.reduce((acc, r) => {
      if (!acc[r.brand]) acc[r.brand] = { brand: r.brand, came: 0, bought: 0 };
      acc[r.brand].came += 1;
      if (r.purchase) acc[r.brand].bought += 1;
      return acc;
    }, {})
  );

  const managersData = Object.values(
    filtered.reduce((acc, r) => {
      if (!acc[r.manager]) acc[r.manager] = { manager: r.manager, came: 0, bought: 0 };
      acc[r.manager].came += 1;
      if (r.purchase) acc[r.manager].bought += 1;
      return acc;
    }, {})
  );

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">CRM ��� �������� ({user.role})</h1>
      <div className="mb-4">
        <input type="date" value={date} onChange={(e) => setDate(e.target.value)} />
        <input placeholder="��'� �볺���" value={clientName} onChange={(e) => setClientName(e.target.value)} />
        <select value={brand} onChange={(e) => setBrand(e.target.value)}>
          <option>��</option>
          <option>��2</option>
          <option>��3</option>
          <option>��4</option>
        </select>
        <button onClick={addRecord}>������</button>
      </div>

      <div className="mb-4">
        <h2 className="font-bold">Գ�����</h2>
        <input placeholder="��������" value={filter.manager} onChange={(e) => setFilter({ ...filter, manager: e.target.value })} />
        <select value={filter.brand} onChange={(e) => setFilter({ ...filter, brand: e.target.value })}>
          <option value="">�� ������</option>
          <option>��</option>
          <option>��2</option>
          <option>��3</option>
          <option>��4</option>
        </select>
        <input type="date" value={filter.from} onChange={(e) => setFilter({ ...filter, from: e.target.value })} />
        <input type="date" value={filter.to} onChange={(e) => setFilter({ ...filter, to: e.target.value })} />
        <button onClick={exportToExcel}>������� � Excel</button>
      </div>

      <table border="1" className="mb-4">
        <thead>
          <tr>
            <th>����</th>
            <th>��������</th>
            <th>�볺��</th>
            <th>�����</th>
            <th>�����</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {filtered.map((r, i) => (
            <tr key={i}>
              <td>{r.date}</td>
              <td>{r.manager}</td>
              <td>{r.clientName}</td>
              <td>{r.brand}</td>
              <td>{r.purchase ? "?" : "?"}</td>
              <td>
                {!r.purchase && <button onClick={() => markPurchase(r.id)}>��������� �������</button>}
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      {user.role === "admin" && (
        <div className="grid grid-cols-2 gap-4">
          <div>
            <h2 className="font-bold mb-2">���������� �� �������</h2>
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={brandsData}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="brand" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Bar dataKey="came" fill="#8884d8" name="�������" />
                <Bar dataKey="bought" fill="#82ca9d" name="������" />
              </BarChart>
            </ResponsiveContainer>
          </div>
          <div>
            <h2 className="font-bold mb-2">���������� �� ����������</h2>
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={managersData}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="manager" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Bar dataKey="came" fill="#8884d8" name="�������" />
                <Bar dataKey="bought" fill="#82ca9d" name="������" />
              </BarChart>
            </ResponsiveContainer>
          </div>
        </div>
      )}
    </div>
  );
}

function LoginForm({ onLogin }) {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  return (
    <div className="p-4">
      <h2 className="text-xl font-bold mb-4">����</h2>
      <input placeholder="����" value={username} onChange={(e) => setUsername(e.target.value)} />
      <input type="password" placeholder="������" value={password} onChange={(e) => setPassword(e.target.value)} />
      <button onClick={() => onLogin(username, password)}>�����</button>
    </div>
  );
}

export default App;
